@include exports('action-buttons/theme') {

    .k-action-buttons {

        .k-button {
            color: inherit;

            &:hover {
                @include appearance( hovered-button );
            }

            &:active {
                @include appearance( pressed-button );
            }
        }

        .k-primary {
            color: $accent;

            &:hover {
                @include appearance( hovered-primary-button );
            }

            &:active {
                @include appearance( pressed-primary-button );
            }
        }
    }

}


