        font-family: 'WebComponentsIcons';
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        text-decoration: none;
        display: inline-block;
        vertical-align: middle;

        &::before {
            vertical-align: baseline;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    .k-i-none::before {
        content: "";
        display: none;
    }

    .k-icon-xs { font-size: ( $icon-size * .5 ); }
