// Disabled
@mixin disabled {
    outline: none;
    cursor: default;
    opacity: $disabled-opacity;
    filter: $disabled-filter;
    pointer-events: none;
}
@include exports("placeholder/disabled") {
    %disabled {
        @if ( mixin-exists( disabled ) ) {
            @include disabled;
        }
    }
}


